import { NextRequest, NextResponse } from 'next/server'
import { readFile } from 'fs/promises'
import { existsSync } from 'fs'
import { join } from 'path'
import { PrismaClient } from '@prisma/client'

const prisma = new PrismaClient()

// Handle CORS preflight requests
export async function OPTIONS() {
  return new NextResponse(null, {
    status: 200,
    headers: {
      'Access-Control-Allow-Origin': 'http://localhost:3000',
      'Access-Control-Allow-Methods': 'GET, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type, Authorization',
    },
  })
}

export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  try {
    const { id } = await params
    const { searchParams } = new URL(request.url)
    const userId = searchParams.get('userId')

    if (!userId) {
      return NextResponse.json({ error: 'User ID is required' }, { 
        status: 400,
        headers: {
          'Access-Control-Allow-Origin': 'http://localhost:3000',
        },
      })
    }

    // First try to find the document in the Document table
    let document = await prisma.document.findFirst({
      where: {
        id,
        userId,
      },
    })

    // If not found in Document table, try to find in Digitized table
    let digitizedDocument = null
    if (!document) {
      digitizedDocument = await prisma.Digitized.findFirst({
        where: {
          originalDocumentId: id,
          userId,
        },
      })

      if (digitizedDocument) {
        // Use digitized document data directly for file access
        document = {
          filePath: digitizedDocument.filePath,
          mimeType: digitizedDocument.mimeType,
          originalName: digitizedDocument.originalName,
        } as any
      }
    }

    if (!document) {
      return NextResponse.json({ error: 'Document not found' }, { 
        status: 404,
        headers: {
          'Access-Control-Allow-Origin': 'http://localhost:3000',
        },
      })
    }

    if (!document.filePath) {
      return NextResponse.json({ error: 'File path not found' }, { 
        status: 404,
        headers: {
          'Access-Control-Allow-Origin': 'http://localhost:3000',
        },
      })
    }

    // Check if file exists
    const fullPath = join(process.cwd(), document.filePath)
    if (!existsSync(fullPath)) {
      return NextResponse.json({ error: 'File not found on disk' }, { 
        status: 404,
        headers: {
          'Access-Control-Allow-Origin': 'http://localhost:3000',
        },
      })
    }

    // Read file
    const fileBuffer = await readFile(fullPath)

    // Set appropriate headers
    const headers = new Headers()
    headers.set('Content-Type', document.mimeType || 'application/octet-stream')
    headers.set('Content-Length', fileBuffer.length.toString())
    headers.set('Content-Disposition', `inline; filename="${document.originalName}"`)
    headers.set('Access-Control-Allow-Origin', 'http://localhost:3000')

    return new NextResponse(fileBuffer, {
      status: 200,
      headers,
    })
  } catch (error) {
    console.error('View file error:', error)
    return NextResponse.json(
      { error: 'Failed to view file' },
      { 
        status: 500,
        headers: {
          'Access-Control-Allow-Origin': 'http://localhost:3000',
        },
      }
    )
  } finally {
    await prisma.$disconnect()
  }
}